import React from 'react';
import { NavLink } from 'react-router-dom';

import { ProductConsumer } from '../../context';
import ShowCartProducts from './showCartProducts';
import DrawerToggleButton from '../SliderMenu/drawerToggleButton';

const Header = ({ drawerClickHandler }) => {
    return (
        <header>
            <div className="toolbar_navigation">
                <div className="toggleBtn_logo">
                    <div className="tollbar_toggle_button">
                        < DrawerToggleButton click={drawerClickHandler} />
                    </div>

                    <div className="toolbar_logo">
                        <span><img src={require('../../images/logo.png')} alt="" /></span>
                    </div>
                </div>

                <div className="toolbar_navigations_items">
                    <ul>
                        <li><NavLink exact={true} to="/" activeStyle={{color: "white"}}>Home </NavLink></li>
                        <li><NavLink to="/games" activeStyle={{color: "white"}}>Games</NavLink></li>
                        <li><NavLink to="/computerHardWare" activeStyle={{color: "white"}}>PC-Hardware</NavLink></li>
                        <li><NavLink to="/laptops" activeStyle={{color: "white"}}>Laptops</NavLink></li>
                        <li><NavLink to="/contacts" activeStyle={{color: "white"}}>Contacts</NavLink></li>
                    </ul>
                </div>
                <div className="bg_cart_header">
                    <ProductConsumer>
                        {value => < ShowCartProducts count={value.countHeaderCart} cart={value.cart}/>}
                    </ProductConsumer>
                </div>
            </div>
        </header>
    )
}
export default Header;


