import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import FixedFormatPrice from '../fixedFormatPrice';

class ShowCartProducts extends Component {
    constructor(props) {
        super(props)

        this.state = {
            price: 0
        }
    }
    render() {
        let orderTotal = 0;
        this.props.cart.map((product) => {
            let a = product.price;
            let b = product.countOrder;
            let result = a * b;
            orderTotal += result;
        })
        //console.log(orderTotal)
        // let firstNums = 0
        // let secondNums = 0;
        // if (orderTotal !== 0) {
        //     orderTotal = orderTotal.toString();
        //     let regex = /^(\d+).(\d+)/g;
        //     let result = regex.exec(orderTotal)
        //     firstNums = result[1];
        //     secondNums = result[2];
        //     //onsole.log(secondNums)
           
        //     let fixedSecondNum = FixedFormatPrice.formatCurrency(Number(secondNums))
            

        //    console.log(fixedSecondNum)
        // }else{
        //     firstNums = '0'
        //     secondNums = '00';
        // }

        return (
            <li><NavLink to="/cart"
            ><div className="count_product">{this.props.count}</div><i className="fas fa-shopping-cart"></i>
                <div className="productNumsHeader">
                    <span>${FixedFormatPrice.formatCurrency(orderTotal)}</span>
                </div>
            </NavLink>
            </li>
        )
    }

}
export default ShowCartProducts;
