import React, { Component, Fragment } from 'react';

import ListChilds from './listChildsAndInfo/listChilds';
import InfoCurrentChild from './listChildsAndInfo/infoCurrentChild';
import Filter from './filter';

class ShowListComponentsAndInfo extends Component {
    constructor(props){
        super(props)
        
        this.state = {
            product: [],
            sort: ''
        }
    }

   

    handlefilteredProducts = (e) => {
        //console.log('handlefilteredProducts')
        //console.log(this.state.product[0].itemOfModel)
        this.setState({
            sort: e.target.value
        });
        this.setState(state => {
            if (state.sort !== '') {
                state.product[0].itemOfModel.sort((a, b) => {
                    if (state.sort === 'lowest') {
                        return (a.price > b.price ? 1 : -1)
                    } else if (state.sort === 'highest') {
                        return (a.price < b.price ? 1 : -1)
                    }
                })
            }
        })
    }
    componentWillReceiveProps(prevState){
        //console.log('componentWillReceiveProps')
        //console.log(prevState)
        this.setState({
            product: prevState.currentClickProducts
            
        })
    }
    render() {
        let {
            handleCurrentClickChild,
            isShowFirstChildInfo,
            currentClickChildInfo,
            data
        } = this.props

     

        let arr = [];
        arr.push(data.boxes[0].items[0].models[0])
        if (this.state.product.length > 0) {
            //console.log('if')
            arr = this.state.product
        }
        //console.log(arr)
        return (
            arr.map((el, key) => {
                return (
                    <Fragment>
                        <div className="current_component_clicked_on_parrent" key={key}>
                            <div className="current_component_name">
                                <h2>{el.model_name}</h2>
                                < Filter handlefilteredProducts={this.handlefilteredProducts} />
                            </div>

                            <div className="current_component_childs" key={key}>
                                <div className="article_childs">
                                    {el.itemOfModel.map((item, key) => {
                                            //console.log(item)
                                        return (
                                            < ListChilds
                                                key={key}
                                                item={item}
                                                handleCurrentClickChild={handleCurrentClickChild}
                                            />
                                        )
                                    })}
                                </div>

                                {
                                    < InfoCurrentChild
                                        isShowFirstChildInfo={isShowFirstChildInfo}
                                        key={key}
                                        showFirstItem={el.itemOfModel[0]}
                                        currentClickChildInfo={
                                            currentClickChildInfo
                                        }
                                    />
                                }
                            </div>
                        </div>
                        <br />
                    </Fragment>
                )
            })
        )
    }


}
export default ShowListComponentsAndInfo;